body {
    height: 100%;
    width: 100%;
    scroll-behavior: smooth;
}

#root {
    width: 100%;
}

body {
    margin: 0;
    font-family: -apple-system, 'Noto Sans KR', BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

:root {
    --bg-main-color: white;
    --bg-footer-color: #0a0936;
    --bg-header-color: #222;
    --bg-flopmenu-color: #070641;
    --color-yellow-light: #fff8b6;
    --color-yellow: #ffd600;
    --main-border-size: 1rem;
    --main-border-padding: 2rem;
    --size-header: 160px;
    --size-header-logo: 55px;
    --size-header-title: 3.5rem;
    --size-header-scrolled: 120px;
    --fs-title: var(--fs-1);
    --fs-1: 2.5rem;
    --fs-2: 2rem;
    --fs-3: 1.75rem;
    --fs-4: 1.5rem;
    --fs-5: 1.25rem;
    --fs-6: 1rem;
    --section-mt: 2rem;
    --section-mt-md: 4rem;
    --section-mt-lg: 6rem;
    --bs-body-font-family: 'Noto Sans KR';
    --table-fs: var(--fs-5);
    --table-padding: 1rem;
    --table-img-max: 90px;
    --slick-dots-size: 1rem;
    --slick-dots-gap: 1.5rem;
    --slick-arrow-size: 2rem;
    --slick-arrow-gap: 2rem;
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

div, header, footer, section {
    box-sizing: border-box;
}

img {
    max-height: 100%;
    max-width: 100%;
}

a {
    text-decoration: none;
    color: inherit;
}

ul {
    margin: 0;
    padding: 0;
}

li {
    list-style: none;
}

h1, h2, h3, h4, h5, h6 {
    margin: 0;
}

h1 {
    font-size: var(--fs-1);
}

h2 {
    font-size: var(--fs-2);
}

h3 {
    font-size: var(--fs-3);
}

h4 {
    font-size: var(--fs-4);
}

h5 {
    font-size: var(--fs-5);
}

h6 {
    font-size: var(--fs-6);
}

.d-flex {
    display: flex;
}

.flex-column {
    flex-direction: column;
}

.flex-1 {
    flex: 1;
}

/*
  Layout
*/
.inner {
    width: 1520px;
    max-width: 90%;
    max-height: 100%;
    margin: 0 auto;
}

.header {
    position: fixed;
    height: var(--size-header);
    width: 100%;
    color: white;
    background: linear-gradient(180deg, rgba(0, 0, 0, 0.1), transparent);
    font-size: var(--fs-5);
    transition: 0.5s;
    z-index: 10;
}

.header.scrolled {
    height: var(--size-header-scrolled);
    background: var(--bg-header-color);
    box-shadow: 1px 1px 10px rgba(0, 0, 0, 0.3);
}

.header .header-logo {
    height: var(--size-header-logo);
}

.header .inner {
    display: flex;
    height: 100%;
    align-items: center;
    gap: 1rem;
}

.header ul.header-nav {
    width: 700px;
    display: flex;
    justify-content: space-between;
}

.header .header-title {
    font-family: 'Melliath';
    color: var(--color-yellow-light);
    font-size: var(--size-header-title);
}

.header ul.header-nav li {
    margin: 0;
    padding: 0;
    text-transform: uppercase;
}

.header ul.header-nav li:after {
    display: block;
    content: '';
    border-bottom: solid 2px var(--color-yellow);
    transform: scaleX(0);
    transition: transform 250ms ease-in-out;
}

.header ul.header-nav li:hover:after {
    transform: scaleX(1);
}

.all-center {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.section > * {
    width: 1520px;
    max-width: 90%;
    margin: 0 auto;
    text-align: center;
    word-break: keep-all;
    z-index: 1;
}

.section .section-bg {
    position: absolute;
    background: rgba(0, 0, 0, 0.5);
    width: 100%;
    height: 100%;
    max-width: 100%;
    z-index: 0;
}

/**
  Section
 */
#section_home {
    position: relative;
    height: 100vh;
    background: url(${___CSS_LOADER_URL_REPLACEMENT_0___}) center / 100% no-repeat;
    background-size: cover;
    color: white;
}

/**
  Animation
 */
@keyframes spin {
    from {
        transform: rotate(0deg);
    }
    to {
        transform: rotate(360deg);
    }
}

